import ajax from './ajax'
// const BASE_URL = "http://test.appmywm.cn"
// const BASE_URL="http://localhost:3000"
const BASE_URL = '/api'
// const BASE_URL=process.env.VUE_APP_API_URL||'/api'
// 1、根据经纬度获取位置详情<br/>
export const reqAddress = geohash => ajax(`${BASE_URL}/position/${geohash}`)
// export const reqAddress = geohash => ajax(`/position/${geohash}`)

// 2、获取食品分类列表<br/>
export const reqCategorys = () => ajax(BASE_URL+'/index_category')
// export const reqCategorys = () => ajax('/index_category')

// 3、根据经纬度获取商铺列表
export const reqShops = ( longitude,latitude) => ajax(BASE_URL+'/shops',{ longitude,latitude})
// export const reqShops = ( longitude,latitude) => ajax('/shops',{ longitude,latitude})

// 4、根据经纬度和关键字搜索商铺列表<br/>
export const reqSearchGoods = (geohash, keyword) => ajax(BASE_URL+'/search_shops', {geohash, keyword})
// export const reqSearchGoods = (geohash, keyword) => ajax('/search_shops', {geohash, keyword})

// 5、获取一次性图形验证码<br/>
// export const reqCaptchas = () => ajax(BASE_URL+'/captcha')

// 6、用户名密码图形验证码登陆
export const reqPwdLogin = ({name, pwd, captcha}) => ajax(BASE_URL+'/login_pwd', {
    name,
    pwd,
    captcha
  }, 'POST')
// export const reqPwdLogin = ({name, pwd, captcha}) => ajax('/login_pwd', {
//   name,
//   pwd,
//   captcha
// }, 'POST')

// 7、发送短信验证码
export const reqSendCode = phone => ajax(BASE_URL+'/sendcode', {phone})
// export const reqSendCode = phone => ajax('/sendcode', {phone})

// 8、手机号验证码登陆<br/>
export const reqSmsLogin = (phone, code) => ajax(BASE_URL+'/login_sms', {phone, code}, 'POST')
// export const reqSmsLogin = (phone, code) => ajax('/login_sms', {phone, code}, 'POST')

// 9、根据会话获取用户信息
export const reqUserInfo = () => ajax(BASE_URL+'/userinfo')
// export const reqUserInfo = () => ajax('/userinfo')

// 10、用户登出
export const reqLogout = () => ajax(BASE_URL+'/logout')
// export const reqLogout = () => ajax('/logout')
/**
 * 获取商家信息
 */
export const reqShopInfo = () => ajax('/info')

/**
 * 获取商家评价数组
 */
export const reqShopRatings = () => ajax('/ratings')

/**
 * 获取商家商品数组
 */
export const reqShopGoods = () => ajax('/goods')




